	const login = require('../../static/utils/utils').Login;
	const query = require('../../static/utils/utils').Query;
	const Login = new login();
	const Query = new query();
	
	import {format} from'../../static/utils/time.js';
	import {dateWorkAdd,taskUpdateBatch,dateWorkQuery} from "../../static/utils/api.js";
	var _this;
	export default {
		data() {
			return {
			   width:"",                  //设置输入框和输入区域的长度
			   userInfo:"",
			   projectId:"",
			   sprintId:"",
			   departmentId:"",
			   taskId:6,                                  //任务的id.
			   taskOrder:"L3",                            //任务的序号 
			   taskName:"购买重量级别的原子弹",            //任务的内容  这三个参数都通过页面传输参数传过来。
			   
               taskStatusArry:["未完成","完成","中止","进行中"],
			   taskDisplayStatus:"未完成",      //默认的任务状态
			   
			   
			   taskState:0,          // 0:未完成 1:完成 2:中止 3:进行中
			   dayWorkTime:"",        //输入的每天工时
			   dayNote:"",            //每天的日报
			   completionDegree:""         ,//每日的完成度
			   
			}
		},
		onShow(){
			_this = this;
			_this.getSystem();
			uni.getStorage({
				key:"userInfo",
				success:(res)=>{
					let id = {
						id:res.data.id
					}
				  Query.findUser(id)
				  .then(data=>{     
					  console.log("查询到的用户信息",data.data.records);
					  _this.userInfo = data.data.records[0];
					  _this.getHadNowDateWork();             //当任务已经存在的时候，需要修改的时候显示原来有的。
				  })
				  .catch(Error=>{
					  uni.showToast({
					  	title:'网络错误',
						duration:5000,
						icon:"none"
					  })
				  })
				},
				fail:()=>{
					uni.redirectTo({
						url:'../login/login'
					})
				}
			})
		},
		
		onLoad(e){
			
		},
		
		methods: {
			
			//获取系统信息设置输入框的长度
			getSystem:function(){
				_this = this;
				uni.getSystemInfo({
					success:(res)=>{
						_this.width = parseInt(res.windowWidth-70)
					}
				})
			},
		   
		    //查询今日有没有打卡，有的就显示，再次提交的时候就更新
			getHadNowDateWork:function(){
				_this = this;
				let dateTime = format(new Date());
			    uni.showLoading({
			    	title:"获取中",
				    success:()=>{
						uni.request({
							url:dateWorkQuery,
							method:"POST",
							data:{
								taskId:_this.taskId,
								userId:_this.userId,
								date:dateTime,
							},
							dataType:'json'
						})
						.then(data=>{
							uni.hideLoading();
							console.log("查找成功",data[1].data.data.records[0]);
						    if(data[1].data.data.records.length){
								_this.dayWorkTime = data[1].data.data.records[0].workTime;
								_this.dayNote = data[1].data.data.records[0].note;
								_this.taskState = 3;
							}
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:500
							})
						})
					}
			    })
			},
		    
		   
		    pickerChange:function(e){
				_this = this;
				let index = e.detail.value;
				_this.taskDisplayStatus = _this.taskStatusArry[index];
				_this.taskState = index;
			},			
			inputDayWokrTime:function(e){
				_this = this;
				let dayWorkTime = e.detail.value;
				let reg = /^[0-9]+$/g;
				if(reg.test(dayWorkTime)){
					_this.dayWorkTime = dayWorkTime;
				}else{
					uni.showToast({
						title:"请输入数字",
						icon:"none",
					    duration:500,
						success:()=>{
							_this.dayWorkTime = "";
						}
					})
				}
			},
			
			inputDayNote:function(e){
				let dayNote = e.detail.value;
				_this.dayNote = dayNote;
			},
			
		    sliderChange:function(e){
				_this = this;
				_this.completionDegree = e.detail.value+'%';
			},
			
			
			//提交每日打卡
			submitDateWork:function(){
				_this = this;
				if(_this.dayNote&&_this.dayWorkTime
				   &&_this.completionDegree&&_this.taskId
				   ){
					let dateTime  = format(new Date());
					uni.showLoading({
						title:"提交中",
						success:()=>{
							uni.request({
								url:dateWorkAdd,
								method:"POST",
								data:{
								  taskId:_this.taskId,
							      userId:_this.userInfo.id,
								  date:dateTime,
								  completionDegree:_this.completionDegree,
								  note:_this.dayNote,
								  workTime:_this.dayWorkTime
								},
								dataType:'json'
							})
							.then(data=>{
								console.log('提交成功',data)
								uni.hideLoading();
								uni.showToast({
									title:"打卡成功",
									icon:"../../static/img/Icon/success.png",
								    duration:500
								})
								if(_this.taskState){  //当选中的状态不为未完成时，更新任务的状态
								   _this.updatetTask();
								}
							})
							.catch(Error=>{
								uni.showToast({
									title:"网络错误",
									icon:"loading",
									duration:500
								})
							})
						}
					})
				}else{
					uni.showToast({
						title:"请完善信息",
						duration:500,
						icon:"none"
					})
				}
			},
			
			//当taskStatus选中为非0状态的时候向任务表中更新状态
			updatetTask:function(){
				_this = this;
				uni.showLoading({
					title:"提交中",
					success:()=>{
						uni.request({
							url:taskUpdateBatch,
							method:"POST",
							data:[{
								id:_this.taskId,
								taskState:_this.taskState
							}],
							dataType:'json'
						})
						.then(data=>{
							console.log("更新任务成功",data)
							uni.hideLoading();
							uni.showToast({
								title:"更新成功",
								icon:"../../static/img/Icon/success.png",
								duration:500
							})
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:1000
							})
						})
					}
				})
			}
			
		}
	}
